

import XInput from 'vux/src/components/x-input/index.vue'

import HeaderBack from '../Common/HeaderBack'

export default {
name: 'register',
components: {
myHeader: HeaderBack,
XInput,
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBC_text:"手机验证",
HBR_icon: " ",
},
telPhoneNum: '',
phoneCode: '',
canSend: true,
sendNum: 60,
}
},
computed:{

},
methods: {
getCodeFn() {
if (this.canSend) {

if (!this.telPhoneNum) {
this.$vux.toast.text('请输入手机号码...', 'success');
return;
}

let valide = this.$refs.pInput.valid;
if (!valide) {
this.$vux.toast.show('输入的号码有误...', 'success');
return;
}
this.canSend = false;
let telPhoneNum = this.telPhoneNum.replace(/\s+/g,"");
this.$http.get("/weixin/phone/sendNumber", { phone: telPhoneNum })
.then((data) => {
if (data === 'success') {
let self = this;
this.$vux.toast.show('发送成功...','success');
let sendTimer = setInterval(function () {
self.sendNum -= 1;
if (self.sendNum === 0) {
self.sendNum = 60;
self.canSend = true;
clearInterval(sendTimer);
}
}, 1000);
} else {
this.canSend = true;
}
})
}
},
submitFn() {
if (!this.telPhoneNum) {
this.$vux.toast.text('请输入手机号码...','top');
return;
} else if (!this.phoneCode) {
this.$vux.toast.text('请输入验证码...','top');
return;
} else {
let telPhoneNum = this.telPhoneNum.replace(/\s+/g,"");
this.$http.post("/weixin/phone/bindSave", { phone: telPhoneNum, number: this.phoneCode })
.then((data) => {
if(!data.success){
this.$vux.toast.show({
type:'warn',
text:'验证失败...',
});
} else {
this.$router.go(-1);
};
})
}

}
},
watch: {
header:{
handler: function (newVal, oldVaal) {

},
deep: true
}
}
}



